.section__portfolio {
  margin: auto;
  padding-top: 40px;
  width: 85%;
  overflow: hidden;

  &__flex {
    margin-top: 20px;
    display: grid;
    gap: 40px;
    grid-template-columns: 1fr 1fr 1fr;

    @media (max-width: 992px) {
      grid-template-columns: 1fr 1fr;
    }
    @media (max-width: 700px) {
      grid-template-columns: 1fr;
    }
  }
}
