.up-button {
  position: fixed;
  bottom: 45px;
  right: 12px;
  background: $color-bg;
  border: solid 1px $color-primary;
  color: $color-primary;
  font-weight: 900;
  font-size: 17px;
  width: 40px;
  height: 40px;
  display: block;
  border-radius: 6px;
  cursor: pointer;
  &:hover i {
    transform: scale(1.1);
    transition: 0.3s;
  }
}
