.section__experience {
  padding: 25px;
  background-image: url("../../images/wallpaper-dev.jpg");
  background-size: cover;
  min-height: 100vh;

  &__text {
    background: linear-gradient($color-primary-variant, $color-bg);
    width: fit-content;
    margin: auto;
    border-radius: 10px;
    backdrop-filter: blur(15px);
    padding: 20px;
    border: solid 2px $color-light;
    transition: 2s;
    &:hover {
      box-shadow: 1px 1px 1px $color-primary;
    }

    & h2 {
      margin: 4px 0 14px 0;
    }
    & ul {
      margin-top: 20px;
      text-align: center;
      border-radius: 40px;
      width: 500px;
      margin: auto;
      display: block;
      width: 100%;

      & li {
        display: block;
        width: 100%;
      }
    }

    @media (max-width: 600px) {
    }
  }

  &__card {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: auto;
    gap: 30px;
    padding: 20px;
    border-radius: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
  }
}
