.navbar {
  &__toggler {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 4px;
    background: $color-bg;
    border: solid 1px $color-primary;
    padding: 6px;
    border-radius: 5px;
    height: 40px;
    width: 40px;
    cursor: pointer;

    &__line {
      background: $color-primary;
      height: 0.2em;
      width: 1.2em;
    }
  }
  &__menu {
    position: fixed;
    top: 100px;
    //right: -80px;
    width: 70px;
    height: 500px;
    background: linear-gradient($color-primary, $color-light);
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    transition: 0.4s;
    transition-timing-function: cubic-bezier(0.5, 1.6, 0.4, 0.7);
    border-radius: 15px 0 0 15px;
    border: solid 3px $color-light;

    a {
      transition: 0.5s;
      cursor: pointer;
    }

    & a:hover {
      transform: scale(1.4);
    }
    & a.active {
      transform: scale(1.4);
    }
  }
}

/*----------------Animation-------------*/

/*-------Clicked---------*/

.navbar__toggler__line.clicked:nth-child(1) {
  transform: rotate(45deg) translate(4px, 4px);
  transition: ease-out 0.2s;
}
.navbar__toggler__line.clicked:nth-child(2) {
  opacity: 0;
}
.navbar__toggler__line.clicked:nth-child(3) {
  transform: rotate(-45deg) translate(6px, -6px);
  transition: ease-out 0.2s;
}

/*-------Unclicked---------*/

.navbar__toggler__line.unclicked {
  transform: rotate(0deg) translate(0);
  transition: cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.5s;
}
