.section__contact {
  margin: auto;
  padding-top: 100px;
  margin-top: 100px;
  width: 80%;
  @media (max-width: 600px) {
    width: 90%;
  }

  & h2 {
    margin-bottom: 100px;
  }

  &__grid {
    display: grid;
    grid-template-columns: 30% 58%;
    gap: 12%;

    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }

    &__infos {
      display: flex;
      flex-direction: column;
      gap: 20px;

      a {
        color: white;
        & article {
          background-color: $color-bg-variant;
          padding: 20px;
          border-radius: 20px;
          text-align: center;
          border: 1px solid transparent;
          transition: 0.3s;

          &:hover {
            background: transparent;
            border-color: $color-primary-variant;
          }
        }
      }
    }

    & form {
      display: flex;
      flex-direction: column;
      gap: 10px;
      margin-bottom: 150px;

      & input,
      textarea {
        padding: 10px;
        border-radius: 10px;
        background: transparent;
        border: 2px solid $color-primary-variant;
        resize: none;
        color: white;
      }

      & button {
        padding: 8px;
        border-radius: 5px;
        background: $color-primary;
        color: $color-bg;
        width: fit-content;
        cursor: pointer;
        position: relative;
        z-index: 1;
      }
      & button:hover {
        color: black;
      }

      & button::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        right: 0;
        height: 100%;
        width: 100%;
        background: $color-light;
        border-radius: 5px;
        z-index: -1;
        transition: transform 0.5s;
        transform-origin: 0 0;
        transition-timing-function: cubic-bezier(0.5, 1.6, 0.4, 0.7);
        transform: scaleX(0);
      }
      & button:hover::before {
        transform: scaleX(1);
      }
    }
  }
}
