.CTA {
  margin-top: 15px;
  display: flex;
  gap: 15px;
  justify-content: center;

  & .contact {
    display: flex;
    align-items: center;
    border-radius: 5px;
    width: max-content;
    background: $color-primary;
    color: $color-bg;
    padding: 8px;
    font-size: 13.33px;
    font-family: Arial, sans-serif;
    position: relative;
    z-index: 1;
  }

  & .contact:hover {
    color: black;
  }

  & .contact::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    height: 100%;
    width: 100%;
    background: $color-light;
    border-radius: 5px;
    z-index: -1;
    transition: transform 0.5s;
    transform-origin: 0 0;
    transition-timing-function: cubic-bezier(0.5, 1.6, 0.4, 0.7);
    transform: scaleX(0);
  }

  & .contact:hover::before {
    transform: scaleX(1);
  }
}

.popup {
  transition: 1s;
  overflow: hidden;
  &__content {
    margin: auto;
    margin-top: 20px;
    width: 340px;
    font-size: 12px;
    padding: 10px;
    border-radius: 10px;
    border: 2px solid $color-primary-variant;
  }
}
