.white-margin {
  padding: 2px;
  background: $color-light;
  border-radius: 10px;
  transition: 0.2s;
  &:hover {
    scale: (1.02);
    box-shadow: 1px 1px 5px $color-primary;
  }

  & .expcard {
    height: 130px;
    width: 200px;
    background: linear-gradient($color-primary-variant, $color-bg);
    border-radius: 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;

    & i {
      color: white;
    }

    &__img {
      background: white;
      border-radius: 50px;

      & img {
        height: 80px;
        width: 80px;
      }
    }
  }
}
