  background-attachment: fixed;
  color: $color-white;
}

section {
  min-height: 100vh;
}

h2 {
  color: $color-primary;
  font-family: "Lora", serif;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 20px;
}
