.projetcard {
  flex-basis: 25%;
  flex-grow: 1;
  min-width: 220px;
  border-radius: 5px;
  padding: 20px;
  background: linear-gradient($color-primary-variant, $color-bg);
  position: relative;
  overflow: hidden;
  transition: 1s;

  &:hover > .img {
    scale: (1.05);
  }
  .img {
    transition: 0.4s;
  }

  &:hover > .link-out {
    bottom: 0;
  }

  &__title {
    font-family: "Lora", serif;
    padding: 5px 10px;
    margin: 0px auto 10px;
    width: fit-content;
    border-width: 3px;
    border-style: solid;
    border-image: linear-gradient($color-primary-variant, $color-primary);
    border-image-slice: 1;
  }

  & img {
    width: 100%;
    object-fit: cover;
    transition: 0.4s;
  }

  & p {
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 5px;
    border-radius: 5px;
    border: solid 1px $color-primary;
    background: linear-gradient($color-primary-variant, $color-bg-variant);
    font-size: 14px;
    text-align: center;
  }

  &__techno {
    & h4 {
      margin: auto;
      width: fit-content;
    }

    &__logo {
      margin: 10px;
      display: flex;
      justify-content: space-around;
    }
  }

  & .link-out {
    width: 100%;
    height: 29%;
    background: rgba(77, 181, 255, 0.8);
    position: absolute;
    bottom: -100%;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: 0.4s;
    border-radius: 10px 10px 0 0;

    & .btn-portfolio {
      display: flex;
      align-items: center;
      font-size: 13.33px;
      font-family: Arial, sans-serif;
      margin: 3px;
      color: $color-primary;
      background: $color-bg;
      padding: 8px;
      border-radius: 5px;
      width: max-content;
      cursor: pointer;
      color: $color-primary;
      position: relative;
      z-index: 1;
    }

    & .btn-portfolio:hover {
      color: black;
    }

    & .btn-portfolio::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      height: 100%;
      width: 100%;
      background: white;
      border-radius: 5px;
      z-index: -1;
      transition: transform 0.5s;
      transform-origin: 0 0;
      transition-timing-function: cubic-bezier(0.5, 1.6, 0.4, 0.7);
      transform: scaleX(0);
    }

    & .btn-portfolio:hover::before {
      transform: scaleX(1);
    }
  }
}
