.about-btn {
  color: $color-primary;
  background: $color-bg;
  border: solid $color-primary 1px;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  position: relative;
  z-index: 1;
}

.about-btn:hover {
  color: $color-bg;
}

.about-btn::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  width: 100%;
  background: $color-primary;
  border-radius: 5px;
  z-index: -1;
  transition: transform 0.5s;
  transform-origin: 0 0;
  transition-timing-function: cubic-bezier(0.5, 1.6, 0.4, 0.7);
  transform: scaleX(0);
}

.about-btn:hover::before {
  transform: scaleX(1);
}

