.header {
  background: linear-gradient($color-primary, $color-bg);
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  position: sticky;
  top: 0;
  z-index: 100;
  padding-left: 40px;
  padding-right: 14px;
  width: 100%;
  // padding: 12px;

  & .title {
    color: white;
    border-radius: 10px;
    padding: 10px;

    & .name {
      font-weight: bold;
      font-size: 20px;
    }

    & p {
      margin: 0;
    }

    @media (max-width: 600px) {
      font-size: 14px;
    }
  }
}
